body {
  background-color: #ececee;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  display: block;
}

.image {
  display: block;
}

.top-container {
  width: 100%;
  background-color: #2196f3;
  position: fixed;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  z-index: 10;
}

.search-form {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-form input {
  margin-right: 10px;

  height: 40px;
  padding-left: 42px;

  border: 1px solid rgba(33, 33, 33, 0.2);
  box-sizing: border-box;
  border-radius: 4px;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:focus,
  &:active {
    border-color: #2196f3;
  }

  &:focus + .form__icon,
  &:active + .form__icon {
    fill: #2196f3;
  }
}

.search-form button {
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.65;
  text-align: center;
  letter-spacing: 0.05em;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border: none;

  padding-right: 20px;
  padding-left: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
}

.body-container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding: 100px;
}

img {
  width: 100px;
}
